<script setup>
import { ref, onMounted, onBeforeUnmount } from "vue";
import { useThemeVars } from "naive-ui";

const themeVars = useThemeVars();
const color = ref("");

// 时间表
let time_table = [
    { num: 1, name: "野火", time: 8 },
    { num: 2, name: "燎原", time: 9 },
    { num: 3, name: "焚燃", time: 10 },
    { num: 4, name: "烬寂", time: 11 },
    { num: 5, name: "沉沦", time: 14 },
    { num: 6, name: "泥盆", time: 15 },
    { num: 7, name: "淤滞", time: 16 },
    { num: 8, name: "垝垣", time: 17 },
    { num: 9, name: "霡霂", time: 19 },
    { num: 10, name: "艨泠", time: 20 },
    { num: 11, name: "瀑雨", time: 21 },
    { num: 12, name: "洪流", time: 22 },
];

// 自动补0
const add_0 = (num, len) => (Array(len).join(0) + num).slice(-len);

// 处理函数
const deal = (hour, minute, second) => {
    min_now.value = add_0(minute, 2);
    sec_now.value = add_0(second, 2);

    // 查找时间表
    let num1 = 0,
        name1 = "休息";
    for (let i = 0; i < time_table.length; i++) {
        const element = time_table[i];
        const e_hour = element.time;
        if (hour == e_hour) {
            num1 = element.num;
            name1 = element.name;
            break;
        }
    }

    num.value = num1;
    name.value = name1;

    // 计算工作状态
    state.value = num1 > 0 && minute < 45;

    // 设置颜色
    if (state.value) color.value = themeVars.value.errorColor;
    else color.value = themeVars.value.warningColor;

    // 总时间
    let total_time;
    if (num1 == 0) total_time = 60 * 60;
    else if (minute < 45) total_time = 45 * 60;
    else total_time = 15 * 60;

    // 已经花费的时间
    let use_time;
    if (num1 > 0 && minute >= 45) use_time = (minute - 45) * 60 + second;
    else use_time = minute * 60 + second;

    // 比例
    percentage.value = parseInt((use_time / total_time) * 100);

    // 计算最大时间
    let minute_max;
    if (num1 > 0 && minute < 45) minute_max = 45;
    else minute_max = 60;

    // 剩余时间
    let min_left1 = minute_max - minute;
    let sec_left1 = 60 - second;

    // 进位
    if (sec_left1 == 60) sec_left1 = 0;
    else min_left1 -= 1;

    min_left.value = add_0(min_left1, 2);
    sec_left.value = add_0(sec_left1, 2);
};

// 设置定时器
let timer;
onMounted(() => {
    var myDate = new Date();
    deal(myDate.getHours(), myDate.getMinutes(), myDate.getSeconds());

    timer = setInterval(() => {
        var myDate = new Date();
        deal(myDate.getHours(), myDate.getMinutes(), myDate.getSeconds());
    }, 1000);
});
// 清除定时器
onBeforeUnmount(() => {
    clearInterval(timer);
});

const state = ref(true);
const name = ref("休息");
const num = ref(0);
const min_now = ref("");
const sec_now = ref("");
const min_left = ref(0);
const sec_left = ref(0);
const percentage = ref(0);
const show_left = ref(true);

// 自定义颜色
const railStyle = ({ focused, checked }) => {
    const style = {};
    if (checked) {
        style.background = "#d03050";
        if (focused) {
            style.boxShadow = "0 0 0 2px #d0305040";
        }
    } else {
        style.background = "#2080f0";
        if (focused) {
            style.boxShadow = "0 0 0 2px #2080f040";
        }
    }
    return style;
};
</script>

<template>
    <!-- 主体 -->
    <n-space vertical align="center" style="margin-top: -60px">
        <h1>Hello Vue 3 + Vue CLI</h1>
        <!-- 时钟 -->
        <n-card style="width: 400px" hoverable>
            <!-- 当前课程名 -->
            <template #header>
                <n-button
                    :focusable="false"
                    :bordered="false"
                    ghost
                    style="font-size: 20px"
                >
                    {{ name }}
                </n-button>
            </template>
            <!-- 模式 -->
            <template #header-extra>
                <n-switch :rail-style="railStyle" v-model:value="state">
                    <template #checked> 工作模式 </template>
                    <template #unchecked> 休息模式 </template>
                </n-switch>
            </template>
            <n-space align="center" justify="space-around">
                <!-- 进度条 -->
                <n-progress
                    type="circle"
                    :color="color"
                    :percentage="percentage"
                    :stroke-width="10"
                />
                <!-- 切换时间 -->
                <n-button
                    :focusable="false"
                    :bordered="false"
                    ghost
                    style="font-size: 20px"
                    @click="show_left = !show_left"
                >
                    <div v-show="!show_left">
                        当前时间 {{ min_now }} : {{ sec_now }}
                    </div>
                    <div v-show="show_left">
                        剩余时间 {{ min_left }} : {{ sec_left }}
                    </div>
                </n-button>
            </n-space>

            <!-- 当前课程编号 -->
            <template #action>
                <n-button :focusable="false" :bordered="false" ghost>
                    第{{ num }}节课
                </n-button>
            </template>
        </n-card>
    </n-space>
</template>

